.popup__save {
    width: 100%;
    height: 50px;
    background: #000000;
    border-radius: 2px;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    cursor: pointer;
    margin-top: calc(48px - 30px);
    padding: 0;
    border: none;
}
.popup__save:hover{
    opacity: 0.8;
    /* transition: opacity 0.2s ease-in-out; */
}
@media screen and (max-width: 320px) {
    .popup__save {
        height: 46px;
        font-size: 14px;
        line-height: 17px;
        margin-top: calc(45px - 30px);
    }
}