.profile__name {
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0;
    padding: 0 18px 16px 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 294px;
}
@media screen and (max-width: 500px) {
    .profile__name{       
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        padding: 0 10px 0 30px;
        max-width: 198px;
    }
}